import io from "socket.io-client"

const DEVELOPMENT = true

const HOST = DEVELOPMENT ? "http://localhost:3000" : ""

let socket

export default {
  init() {
    socket = io(HOST)
  },
  getSocket() {
    return socket
  },
  connectAuth({ token }) {
    socket.emit("connectAuth", { token })
  },
  sendMessage({ token, to, content, messageClientId }) {
    socket.emit("send-message", {
      token,
      to,
      content,
      messageClientId,
    })
  },
  connect() {
    socket.connect()
  },
  disconnect() {
    if (!socket) return
    socket.disconnect()
  },
}
